:root {
  --background: #2D2D2D;
  --clock-color: #9C9E9B;
  --emphasis: #606060;
  --secondary-emphasis: #aeaeae;
  color: var(--clock-color);
}

body {
  background-color: var(--background);
  overflow:hidden;
}

h1 {
  color: black;
}

.logo {
  opacity: .4;
  position: absolute;
  top: 10px;
  right: 10px;
  z-index: 100;
}
.logo-back {
  z-index: 99;
  position: absolute;
  top: 10px;
  right: 10px;
}

.clock {
  color: var(--clock-color);
}

.nonselectable {
  user-select: none
}

.view-swap-button {
  position: absolute;
  bottom: 0;
  left: 35%;
  width: 30%;
  min-width: 150px;
  height: 3rem;
  background-color: var(--clock-color);
  border-radius: 5px 5px 0 0;
}

.view-swap-button:active {
  background-color: var(--emphasis);
}

.view-swap-button:active > .view-swap-label {
  color: var(--secondary-emphasis);
}

.view-swap-label {
  width: fit-content;
  color: var(--background);
  position: relative;
  top: 0;
  left: 50%;
  transform: translate(-50%, -50%);
}

.calendar-separator {
  position: absolute;
  top: 15%;
  height: 75%;
  width: 2px;
  background-color: var(--clock-color);
}

.calendar-weather-view {
  transform: translate(-50%, -50%);
  position: absolute;
  top: 20%;
  width: fit-content;
  text-align: center;
  user-select: none
}

.weather-1 {
  left: 12.5%;
}

.weather-2 {
  left: 37.5%;
}

.weather-3 {
  left: 62.5%;
}

.weather-4 {
  left: 87.5%;
}

.line-1 {
  left: 25%;
}

.line-2 {
  left: 50%;
}

.line-3 {
  left: 75%;
}
